import React, { Component } from 'react';

class App extends Component {
  render() {
    return (
      <div className="dropdown_menu">
        <div className="dropdown field_box block_field">
          <div className="option_box" >
          <div className="label_box" >
            <span className="label">  </span>
              <span className="icon"><img alt="search" src="/brief/002-up-arrow-angle.svg" /></span>
          </div>
              <span className="gray_icon"><img alt="search" src="/images/placeholder.svg" /></span>
          </div>
          <div className="dropdown-content">
            <p><span className="circle"></span> </p>
            <p><span className="circle"></span> </p>
            <p><span className="circle"></span> </p>
            <p><span className="circle"></span> </p>
            <p><span className="circle"></span> </p>
          </div>
        </div>
        <div className="dropdown field_box price_field">
          <div className="option_box" >
          <div className="label_box" >
            <span className="label">   </span>
              <span className="icon"><img alt="search" src="/brief/002-up-arrow-angle.svg" /></span>
          </div>
              <span className="gray_icon"><img alt="search" src="/images/placeholder.svg" /></span>
          </div>
          <div className="dropdown-content">
            <p><span className="circle"></span></p>
            <p><span className="circle"></span> 30 000 $</p>
            <p><span className="circle"></span> 40 000 $</p>
            <p><span className="circle"></span> 60 000 $</p>
            <p><span className="circle"></span> 100 000 $</p>
          </div>
        </div>
        <div className="dropdown field_box area_field">
          <div className="option_box" >
          <div className="label_box" >
            <span className="label">   </span>
              <span className="icon"><img alt="search" src="/brief/002-up-arrow-angle.svg" /></span>
          </div>
              <span className="gray_icon"><img alt="search" src="/images/placeholder.svg" /></span>
          </div>
          <div className="dropdown-content">
            <p><span className="circle"></span> 30  40</p>
            <p><span className="circle"></span> 40  50</p>
            <p><span className="circle"></span> 50  60</p>
            <p><span className="circle"></span> 70  80</p>
            <p><span className="circle"></span> 80  90</p>
            <p><span className="circle"></span> 90  100</p>
            <p><span className="circle"></span> 100</p>
          </div>
        </div>
        <div className="dropdown field_box rooms_field">
          <div className="option_box" >
          <div className="label_box" >
            <span className="label">   </span>
              <span className="icon"><img alt="search" src="/brief/002-up-arrow-angle.svg" /></span>
          </div>
              <span className="gray_icon"><img alt="search" src="/images/placeholder.svg" /></span>
          </div>
          <div className="dropdown-content">
            <p><span className="circle"></span>1 </p>
            <p><span className="circle"></span>2 </p>
            <p><span className="circle"></span>3 </p>
          </div>
        </div>
        <div className="dropdown field_box choose_field">
            <div className="option_box" >
              <div className="label_box" >
                <span className="label">  </span>
              </div>
              <span className="gray_icon"><img alt="search" src="/images/placeholder.svg" /></span>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



